import {Offcanvas, CloseButton} from 'react-bootstrap';

interface MenuProps{
  show:boolean;
  handleClose: () => void;
}

function Menu(props:MenuProps): JSX.Element {
  return (
    <Offcanvas show={props.show} onHide={props.handleClose} placement="end" style={{ background: "#5166BF" }}>
      <Offcanvas.Header>
        <CloseButton variant='white' onClick={props.handleClose} />
      </Offcanvas.Header>
      <Offcanvas.Body>
        <div className="offcanvasWrap">
          <h3>SITEMAP</h3>
        </div>
        <ul className="Menu">
          <li className="list">
            <p></p>
            <ul>
              <li>SNS</li>
              <li></li>
              <li></li>
            </ul>
          </li>
          <li className="list">
            <p></p>
            <ul>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </li>
          <li className="list">
            <p></p>
          </li>
          <li className="list">
            <p></p>
            <ul>
              <li></li>
              <li></li>
              <li>1:1</li>
              <li></li>
            </ul>
          </li>
          <li className="list">
            <p></p>
            <ul>
              <li> </li>
            </ul>
          </li>
        </ul>
        <ul className="loginMenu">
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </Offcanvas.Body>
    </Offcanvas>
  )
}

export default Menu;