import { useState } from 'react';
import { Nav, Form, Modal} from 'react-bootstrap';

import '../css/Nav.css';


function Example(): JSX.Element {
  const [show, setShow] = useState<boolean>(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [checked1, setChecked1] = useState<boolean>(false);
  const [checked2, setChecked2] = useState<boolean>(false);
  const [view, setView] = useState(true);

  return (
    <>
      <Nav.Link onClick={handleShow} href="#" className="loginLink"></Nav.Link>

      <Modal show={show} onHide={handleClose} centered>
        <Modal.Body>
          {
            view === true ?
              (<Step1
                checked1={checked1}
                setChecked1={setChecked1}
                checked2={checked2}
                setChecked2={setChecked2} />) :
              (<Step2 />)
          }
        </Modal.Body>
        <Modal.Footer>
          <button
            type="submit"
            onClick={() => { checked1 && checked2 === true ? setView(false) : alert("    ")}}
            className={"nextBtn"}> </button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

interface SignProps{
  setChecked1(arg0: boolean): unknown;
  setChecked2(arg0: boolean): unknown;
  checked1:boolean;
  checked2:boolean;
}

function Step1(props:SignProps):JSX.Element {
  return (
    <>
      <div className="stepbox">
        <span>Step 1.</span>
        <p>  .</p>
      </div>
      <div className="agreebox">
        <Form>
          <Form.Group>
            <div className="tit">
              <p>  <span>*</span></p>
              <p className="more"></p>
            </div>
            <button
              onClick={(e) => { props.setChecked1(!props.checked1); e.preventDefault() }}
              className={props.checked1 ? "checked" : ""}>.</button>
            <div className="tit">
              <p><span>*</span></p>
              <p className="more"></p>
            </div>
            <button
              onClick={(e) => { props.setChecked2(!props.checked2); e.preventDefault() }}
              className={props.checked2 ? "checked" : ""}>.</button>
          </Form.Group>
        </Form>
      </div>
    </>
  )
}

function Step2(): JSX.Element {
  // 1: Step2  Step1 
  // 2: Step2 required   
  return (
    <>
      <div className="stepbox">
        <span>Step 2.</span>
        <p>  .</p>
      </div>
      <Form className="signUp">
        <Form.Group>
          <Form.Label><span>*</span></Form.Label>
          <Form.Control required type="text" />
        </Form.Group>
        <Form.Group>
          <Form.Label><span>*</span></Form.Label>
          <Form.Control required type="text"/>
        </Form.Group>
        <Form.Group>
          <Form.Label><span>*</span></Form.Label>
          <Form.Control required type="text"/>
        </Form.Group>
        <Form.Group className="pw">
          <Form.Label><span>*</span></Form.Label>
          <Form.Control required type="password"/>
          <p className="rule">, ,   2  8 </p>
        </Form.Group>
        <Form.Group>
          <Form.Label> <span>*</span></Form.Label>
          <Form.Control required type="password"/>
        </Form.Group>
        <Form.Group>
          <Form.Label><span>*</span></Form.Label>
          <Form.Control required type="email"/>
        </Form.Group>
      </Form>
    </>
  )
}

export default Example;